
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');

body {
  font-family: 'Noto Sans JP', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  line-height: 1.6; /* Improve readability */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9f9f9;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.App-header {
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  padding: 20px 0;
  box-shadow: 0 2px 5px rgba(0,0,0,0.05); /* Subtle shadow */
}

.App-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo h1 {
  margin: 0;
  font-size: 24px;
  color: #003366;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  display: inline-block;
  margin: 0 25px; /* Further increased spacing */
}

nav ul li a {
  color: #333;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  position: relative; /* For underline effect */
  padding-bottom: 5px; /* Space for underline */
  transition: color 0.3s ease, border-bottom 0.3s ease; /* Smooth transition */
}

nav ul li a:hover {
  color: #003366; /* Change color on hover */
}

nav ul li a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #003366; /* Underline color */
  transition: width 0.3s ease; /* Smooth transition */
}

nav ul li a:hover::after {
  width: 100%; /* Expand underline on hover */
}

.hero {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent), url('./images/hero-image.jpg');
  background-size: cover;
  background-position: center;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.hero-text h2 {
  font-size: 72px; /* Larger font size */
  margin: 0;
  font-weight: bold;
  color: white;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.8); /* Stronger shadow */
}

.hero-text p {
  font-size: 28px; /* Larger font size */
  margin: 10px 0 0 0;
  color: white;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.8); /* Stronger shadow */
}

main {
  padding: 40px 0;
}

section {
  margin-bottom: 40px;
}

h2 {
  font-size: 32px;
  color: #003366;
  margin-bottom: 20px;
  text-align: center;
}

.news-grid, .blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.news-item, .blog-item, .schedule-item {
  background-color: #fff;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.news-item h3, .blog-item h3, .schedule-item h3 {
  margin-top: 0;
  color: #003366;
}

footer {
  background-color: #003366;
  color: white;
  padding: 20px 0;
  text-align: center;
}



/* Responsive Design */
@media (max-width: 768px) {
  .App-header .container {
    flex-direction: column;
    text-align: center;
  }

  nav ul li {
    margin: 10px 10px;
  }

  .hero {
    height: 300px;
  }

  .hero-text h2 {
    font-size: 40px;
  }

  .hero-text p {
    font-size: 18px;
  }

  h2 {
    font-size: 24px;
  }

  .news-grid, .blog-grid {
    grid-template-columns: 1fr;
  }

  }
