/* Team.css */

.captain-manager {
  font-size: 1.2rem; /* Slightly smaller than 1.5rem */
  color: #555; /* A bit lighter than #333 */
  margin-bottom: 0.5rem; /* Adjust spacing */
}

.year-section h4 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

/* Team.css */

#team {
  padding: 2rem 0;
}

.year-section {
  margin-bottom: 3rem;
}

.year-section h3 {
  font-size: 2rem;
  color: #003366;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #003366;
  padding-bottom: 0.5rem;
}

.team-grid h4 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.members-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
}

.team-member-card {
  background-color: #fff; /* Lighter background */
  padding: 1.5rem;
  border-radius: 12px; /* More rounded corners */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); /* Stronger, softer shadow */
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transition */
}

.team-member-card:hover {
  transform: translateY(-5px);
}

.team-member-card p {
  margin: 0;
  font-size: 1.1rem;
  color: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .year-section h3 {
    font-size: 1.5rem;
  }

  .year-section h4 {
    font-size: 1.2rem;
  }

  .captain-manager {
    font-size: 1rem;
  }

  .members-grid {
    grid-template-columns: 1fr;
  }

  .team-member-card {
    padding: 1rem;
  }

  .team-member-card p {
    font-size: 1rem;
  }
}
